.App {
  padding-bottom:150px;
  overflow: hidden;
  margin-top: -400px;
}
.Channy {
  float:right;
  margin-top: -400px;
  z-index: 0;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.red {
  background: #76f783 !important;
}

.yellow {
  background: #7c091b !important;
}

.blue {
  background-image:url(https://s1.piq.land/2015/10/03/R0UsyFtPiLGFxhgg4azLGZZr_100x100.png) !important;
}

.dead {
  background-image:url(https://lessonpix.com/drawings/1964/100x100/Chicken+Nuggets.png) !important;
}

.App-header {
  background-color:white;
  height: 450px;
  width: 500px;
  padding-top: 50px;
  margin-left: 800px;
  color: #6cc661;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.board-row {
  clear: both;
  content: "";
  display: table;
  margin-left: 20%;
}


.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 100px;
  height: 100px;
  margin-right: 20px;
  margin-top: 20px;
  padding: 0;
  text-align: center;
  width: 100px;
}



